.bsc-breadcrumb > span:last-child .bsc-breadcrumb-item-separator {
  display: none;
}
.bsc-breadcrumb-item-separator {
  margin: 0 8px;
  color: #d7dde4;
}
.bsc-breadcrumb-item-link > .ivu-icon + span {
  margin-left: 4px;
}



